}

.categories-btn:hover, .categories-btn:focus, .categories-btn:active {
  color: #f8f8f8;
  background-color: #9bb537;
}

.categories-list {
  flex-direction: column;
  gap: 18px;
  max-width: 100%;
  height: 198px;
  list-style: none;
  display: flex;
}

.js-categories-item-btn {
  color: #0505054d;
  cursor: pointer;
