.categories-scrollbar {
  overflow-y: scroll;
}

.categories-scrollbar::-webkit-scrollbar {
  width: 8px;
  height: 116px;
}

.categories-scrollbar::-webkit-scrollbar-track {
  border-radius: 12px;
}

.categories-scrollbar::-webkit-scrollbar-thumb {
