.filter-form-wrapper {
  width: 335px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.filter-form {
  flex-wrap: wrap;
  gap: 14px;
  display: flex;
}

.filter-item {
  max-width: 100%;
}

.filter-item-tittle {
  color: #05050580;
  margin-bottom: 8px;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.17;
  display: block;
}

.filter-form-input {
  color: #05050580;
  border: 1px solid #05050533;
  border-radius: 15px;
  max-width: 100%;
  padding: 14px 122px 14px 48px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
}

.filter-form-input-wrap {
  display: block;
  position: relative;
}

.filter-input-search-icon {
  stroke: #050505;
  fill: #050505;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translate(0, -50%);
}

.filter-input-reset-container {
  position: absolute;
  top: 50%;
  right: 18px;
  transform: translate(0, -50%);
}

.filter-input-reset-btn {
  cursor: pointer;
  background: none;
  border: #0000;
  width: 16px;
  height: 16px;
  display: none;
}

.filter-input-reset-btn:focus, .filter-input-reset-btn:hover {
  scale: 1.2;
}

.filter-form-input:not(:placeholder-shown), .filter-form-input:focus, .filter-form-input:active {
  border: 1px solid #05050533;
  outline: none;
}

.filter-form-input:not(:placeholder-shown), .filter-form-input:focus, .filter-form-input:active ~ .filter-input-search-icon {
  stroke: #9bb537;
}

.filter-form-input:not(:placeholder-shown), .filter-input-reset-container .filter-input-reset-btn {
  display: inline-block;
}

.filter-input-reset-icon {
  stroke: #050505;
  fill: #050505;
  width: 16px;
  height: 16px;
}

.filter-item-time {
  width: 160px;
  display: block;
}

.filter-item-area {
  width: 161px;
  display: block;
}

.filter-item-ingredients {
  width: 188px;
  display: block;
}

.filter-form-select {
  -webkit-appearance: none;
  appearance: none;
  color: #0505054d;
  border: 1px solid #05050533;
  border-radius: 15px;
  width: 100%;
  height: 46px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
  display: block;
