:root {
  --footer-bg-color: #9bb537;
  --footer-text-color: #dcee92;
  --footer-link-color: #292424;
  --footer-link-bg-color: #cfcece;
  --btn-to-top-bg-color: #f46060;
  --btn-to-top-fill-color: #35f2c9;
  --focus-visible-box-shadow-color: #00000080;
  --btn-to-top-hover-bg-color: #ff7373;
}

input, h3, p, a, span, li {
  font-family: Inter, sans-serif;
  transition: all .3s;
}

h3 {
  margin-top: 0;
  font-size: 1.75em;
  font-weight: 700;
}

p {
  margin-bottom: 1em;
  font-weight: 400;
  line-height: 1.6;
}

a {
  color: var(--focus-visible-box-shadow-color);
  font-weight: 500;
  text-decoration: none;
}

a:hover, a:focus {
  color: #0056b3;
  text-decoration: underline;
}

.site-footer, .feedback-section button, .footer-media-link, .show-btn {
  transition: all .3s;
}

.show-btn:active {
  filter: brightness(1.2);
}

.show-btn:hover, .show-btn:focus {
  background-color: var(--btn-to-top-hover-bg-color);
  box-shadow: 0 0 0 3px var(--focus-visible-box-shadow-color);
  outline: none;
  transform: scale(1.05);
}

.site-footer {
  background-color: var(--footer-bg-color);
  color: var(--footer-text-color);
  padding: 20px 0;
}

.footer-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-section {
  text-align: center;
  padding: 10px;
}

.feedback-section {
  text-align: center;
  background-color: #ffffff1a;
  border-radius: 5px;
  margin: 20px 0;
  padding: 20px;
}

.feedback-section h3, .feedback-section p {
  margin-bottom: 10px;
}

.feedback-section input[type="email"] {
  border: 1px solid var(--footer-text-color);
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
}

.feedback-section button {
  background-color: var(--footer-bg-color);
  cursor: pointer;
  color: var(--footer-text-color);
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
}

.feedback-section button:hover {
  background-color: var(--btn-to-top-bg-color);
}

.button-gap {
  margin-top: 15px;
}

.f-list {
  justify-content: space-between;
  gap: 20px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.footer-media-item {
  margin: 0;
}

.footer-media-link {
  color: var(--footer-text-color);
  background-color: var(--footer-link-bg-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.footer-media-icon {
  fill: currentColor;
  width: 24px;
  height: 24px;
}

.footer-media-link:hover, .footer-media-link:focus {
  background-color: var(--footer-text-color);
  outline: none;
}

.footer-media-link:focus-visible {
  box-shadow: 0 0 0 3px var(--focus-visible-box-shadow-color);
}

.show-btn {
  background-color: var(--btn-to-top-bg-color);
  fill: var(--btn-to-top-fill-color);
  text-align: center;
  cursor: pointer;
  z-index: 100;
  opacity: .5;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 16px;
  transition: background-color .3s, transform .3s;
  display: none;
  position: fixed;
  bottom: 10px;
  right: 10px;
  transform: translateY(10px);
}

.show-btn.is-visible {
  opacity: .6;
  display: flex;
  transform: translateY(0);
}

@media screen and (min-width: 320px) {
  .show-btn {
    width: 40px;
    height: 40px;
  }
}

@media screen and (min-width: 768px) {
  .footer-content {
    flex-direction: row;
    justify-content: space-between;
  }

  .footer-section:first-child {
    text-align: left;
  }

  .f-list {
    gap: 30px;
  }

  .show-btn {
    width: 50px;
    height: 50px;
  }
}

@media screen and (min-width: 1280px) {
  .footer-content {
    padding: 0 40px;
  }

  .footer-section {
    padding: 15px;
  }

  .show-btn {
    width: 55px;
    height: 55px;
  }
}

